<template>
  <div class="home-container">
    <van-nav-bar title="黑马头条" fixed/>

    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" :disabled="finished">
        <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        >
            <ArticleInfo v-for="item in artlist" :key="item.id"
            :title="item.title"
            :author="item.aut_name"
            :cmt-count="item.comm_count"
            :time="item.pubdate"
            :cover="item.cover"></ArticleInfo>
        </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getArticleAPI } from '@/api/articleAPI.js'
import ArticleInfo from '@/components/Article/ArticleInfo.vue'

export default {
    name: 'Home',
    data () {
        return {
            page: 1,
            limit: 10,
            artlist: [],
            // 节流 冷却一段时间
            loading: true,
            finished: false,
            isLoading:false
        }
    },
    created () {
        this.initArticleList()
        // console.log(this.page);
    },
    methods: {
        async initArticleList(isrefresh) {
            const { data: res} = await getArticleAPI(this.page,this.limit)
            // console.log(res);
            // this.artlist = res
            if (isrefresh) {
                this.artlist = [...res, ...this.artlist],
                this.isLoading = false
            } else {
                this.artlist = [...this.artlist, ...res]
                this.loading = false
            }
            
             if(res.length === 0) this.finished = true
        },
        onLoad() {
            console.log('触发load事件');
            this.page++
            this.initArticleList()
        },
        onRefresh() {
            console.log('触发了下拉刷新操作！');
            this.page++
            this.initArticleList(true)
            // this.onLoad()
        }
    },
    components: {
        ArticleInfo
    }
}
</script>

<style lang="less" scoped>
.home-container{
    padding: 46px 0 50px 0;
}
</style>
